/* Основной контейнер меню */
.player-menu {
    position: fixed;
    bottom: 15px;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% - 30px);
    max-width: 670px;
    height: 50px;
    background-image: url('img/ui/menubg.png');
    background-size: cover;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    transition: all 0.3s cubic-bezier(0.2, 0, 0.1, 1);
  }
  
  .player-menu:hover {
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.25);
    transform: translateX(-50%) translateY(-2px);
  }
  
  /* Левая и правая секции */
  .menu-left {
    flex: 1;
  }
  
  .menu-right {
    position: relative;
  }
  
  /* Контейнер для BPM-кнопки */
  .bpm-container {
    position: relative;
  }
  
  /* Кнопка BPM */
  .bpm-button {
    background: linear-gradient(135deg, #222, #333);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 6px;
    color: white;
    width: 60px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    font-weight: bold;
    font-size: 16px;
    font-family: 'Arial', sans-serif;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.4);
    position: relative;
    overflow: hidden;
  }
  
  .bpm-button:hover {
    background: linear-gradient(135deg, #333, #444);
    transform: translateY(-1px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }
  
  /* Пульсирующий эффект после нажатия */
  .bpm-button:active::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    opacity: 1;
    animation: pulse 0.6s ease-out;
  }
  
  @keyframes pulse {
    0% {
      transform: translate(-50%, -50%) scale(0);
      opacity: 1;
    }
    80% {
      transform: translate(-50%, -50%) scale(1.5);
      opacity: 0.2;
    }
    100% {
      transform: translate(-50%, -50%) scale(2);
      opacity: 0;
    }
  }
  
  /* Эффект свечения для кнопки */
  .bpm-button::before {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    border-radius: 8px;
    background: linear-gradient(45deg, #00a040, transparent, #00c050);
    z-index: -1;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .bpm-button:hover::before {
    opacity: 0.5;
    animation: rotate 2s linear infinite;
  }
  
  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  /* Выпадающее меню */
  .bpm-menu {
    position: absolute;
    top: -200px; /* Расположение выше */
    right: 0;
    width: 110px;
    background: linear-gradient(135deg, #00A040, #00C050);
    border-radius: 10px;
    overflow: hidden;
    z-index: 101;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.4);
    animation: slideDown 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
    transform-origin: top center;
    border: 1px solid rgba(255, 255, 255, 0.15);
  }
  
  @keyframes slideDown {
    0% { 
      opacity: 0; 
      transform: translateY(-10px) scale(0.95); 
    }
    70% { 
      transform: translateY(3px) scale(1.01); 
    }
    100% { 
      opacity: 1; 
      transform: translateY(0) scale(1); 
    }
  }
  
  /* Анимация закрытия меню */
  .bpm-menu.closing {
    animation: slideUp 0.2s ease-in forwards;
  }
  
  @keyframes slideUp {
    0% { 
      opacity: 1; 
      transform: translateY(0) scale(1); 
    }
    100% { 
      opacity: 0; 
      transform: translateY(-10px) scale(0.95); 
    }
  }
  
  /* Опции BPM */
  .bpm-option {
    padding: 12px 0;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    position: relative;
    overflow: hidden;
  }
  
  .bpm-option.prev {
    color: #FFD700;
    font-size: 15px;
    background: rgba(0, 0, 0, 0.05);
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    animation: fadeIn 0.3s ease-out 0.1s backwards;
  }
  
  .bpm-option.current {
    color: white;
    font-size: 20px;
    font-weight: bold;
    background: rgba(0, 0, 0, 0.15);
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.4);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    animation: fadeIn 0.3s ease-out backwards;
  }
  
  .bpm-option.next {
    color: rgba(255, 255, 255, 0.9);
    font-size: 15px;
    background: rgba(0, 0, 0, 0.05);
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    animation: fadeIn 0.3s ease-out 0.2s backwards;
  }
  
  @keyframes fadeIn {
    from { opacity: 0; transform: translateX(10px); }
    to { opacity: 1; transform: translateX(0); }
  }
  
  /* Эффект волны при нажатии */
  .bpm-option:active::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 150px;
    height: 150px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    animation: ripple 0.5s linear;
  }
  
  @keyframes ripple {
    to {
      transform: translate(-50%, -50%) scale(1);
      opacity: 0;
    }
  }
  
  /* Переходы при наведении */
  .bpm-option:hover {
    background: rgba(255, 255, 255, 0.1);
    transform: translateY(-2px);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  /* Анимация при изменении значений */
  .bpm-option.updating {
    animation: updateBounce 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  }
  
  @keyframes updateBounce {
    0% { transform: scale(1); }
    50% { transform: scale(1.1); }
    100% { transform: scale(1); }
  }
  
  /* Медиа-запросы */
  @media (max-width: 480px) {
    .player-menu {
      height: 45px;
      bottom: 10px;
    }
  
    .bpm-button {
      width: 55px;
      height: 32px;
      font-size: 15px;
    }
  
    .bpm-menu {
      width: 100px;
    }
  
    .bpm-option {
      padding: 14px 0;
    }
  }
  
  /* Особая анимация при смене BPM */
  @keyframes bpmChanged {
    0% { transform: scale(1); }
    30% { transform: scale(1.15); color: #fff; text-shadow: 0 0 8px rgba(255, 255, 255, 0.8); }
    100% { transform: scale(1); }
  }
  
  .bpm-value-changing {
    animation: bpmChanged 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }